<template>
  <div class="card has-background-dark is-shadowless">
    <div class="card-content">
      <div class="columns is-multiline mb0">
        <div class="column is-full has-text-light pr0 pl0" @click="navbarToggleFn">
          Performance
          <span class="icon is-pulled-right">
            <i class="material-icons-round">keyboard_arrow_down</i>
          </span>
        </div>
      </div>
      <div class="columns is-multiline" v-if="nav.isPerformance">
        <div class="column is-full pr0 pl0">
          <div class="control">
            <select>
              <option disabled>Select attendance rate</option>
              <option :value="n+9" v-for="n in 91" :key="n">{{n+9}} %</option>
            </select>
            <label class="label">
              Attendance rate
              <i class="material-icons-round is-size-6 cursor is-right">help_outline</i>
            </label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <input
              class="input"
              type="text"
              value="Enter Amount"
              oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
            />
            <label class="label">
              Avg
              savings/member/mtg
            </label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <select>
              <option disabled>Select # of days</option>
              <option :value="n" v-for="n in 12" :key="n">{{n}} Months</option>
            </select>
            <label class="label">
              Avg loan repayment
              time
            </label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <select>
              <option disabled>Select default rate</option>
              <option :value="n-1" v-for="n in 26" :key="n">{{n-1}}</option>
            </select>
            <label class="label">Default rate</label>
            <i class="bar"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Policy",
  data() {
    return {};
  },
  computed: {
    nav() {
      return this.$store.getters.nav;
    }
  },

  methods: {
    navbarToggleFn() {
      this.$store.getters.nav.isPerformance = !this.$store.getters.nav
        .isPerformance;
      this.$store.dispatch(
        "clickPeformance",
        this.$store.getters.nav.isPerformance
      );
    }
  }
};
</script>

<style scoped>
.card {
  width: 100%;
}
</style>