<template>
  <div>
    <h4 class="is-size-4 has-text-primary">Results</h4>

    <div class="columns is-multiline mt15">
      <div class="column is-full" @click="returnOnSavingsSelectFn">
        <div
          class="card has-background-white"
          :class="isReturnOnSavings == true ? 'is-active-card' : ''"
        >
          <div class="card-content pt20 pb20 pr20 pl20">
            <p class="is-size-6 has-text-weight-semibold">Return on Savings</p>
            <h4 class="is-size-4 has-text-weight-semibold mt5">23.1%</h4>
          </div>
        </div>
      </div>
      <div class="column is-full" @click="utilizationSelectFn">
        <div
          class="card has-background-white"
          :class="isUtilization == true ? 'is-active-card' : ''"
        >
          <div class="card-content pt20 pb20 pr20 pl20">
            <p class="is-size-6 has-text-weight-semibold">Loan Fund Utilization</p>
            <h4 class="is-size-4 has-text-weight-semibold mt5">67.4%</h4>
          </div>
        </div>
      </div>
      <div class="column is-full" @click="loanSizeSelectFn">
        <div class="card has-background-white" :class="isLoanSize == true ? 'is-active-card' : ''">
          <div class="card-content pt20 pb20 pr20 pl20">
            <p class="is-size-6 has-text-weight-semibold">Average Loan Size</p>
            <h4 class="is-size-4 has-text-weight-semibold mt5">238,125 TZS</h4>
          </div>
        </div>
      </div>
      <div class="column is-full" @click="memberSavingsSelectFn">
        <div
          class="card has-background-white"
          :class="isMemberSavings == true ? 'is-active-card' : ''"
        >
          <div class="card-content pt20 pb20 pr20 pl20">
            <p class="is-size-6 has-text-weight-semibold">Avg Member Savings</p>
            <h4 class="is-size-4 has-text-weight-semibold mt5">800,000 TZS</h4>
          </div>
        </div>
      </div>
      <div class="column is-full" @click="memberShareoutSelectFn">
        <div
          class="card has-background-white"
          :class="isMemberShareout == true ? 'is-active-card' : ''"
        >
          <div class="card-content pt20 pb20 pr20 pl20">
            <p class="is-size-6 has-text-weight-semibold">Avg Member Shareout</p>
            <h4 class="is-size-4 has-text-weight-semibold mt5">1,000,000 TZS</h4>
          </div>
        </div>
      </div>
      <div class="column is-full pb30">
        <center>
        <button class="button is-primary is-rounded glow-effect" style="margin:0 auto;">
          <span
            class="has-text-white has-text-weight-semibold pr5"
            style="margin-top: -5px; font-size: 13px"
          >View More Results</span>
          <i class="material-icons-round has-text-white" style="margin-top: -5px">expand_more</i>
        </button>
        </center>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Result",

  data() {
    return {
      isReturnOnSavings: true,
      isUtilization: false,
      isLoanSize: false,
      isMemberSavings: false,
      isMemberShareout: false
    };
  },

  methods: {
    returnOnSavingsSelectFn() {
      this.isReturnOnSavings = true;
      this.isUtilization = false;
      this.isLoanSize = false;
      this.isMemberSavings = false;
      this.isMemberShareout = false;

      this.$store.dispatch("headerName", "Return on Savings");
    },
    utilizationSelectFn() {
      this.isReturnOnSavings = false;
      this.isUtilization = true;
      this.isLoanSize = false;
      this.isMemberSavings = false;
      this.isMemberShareout = false;

      this.$store.dispatch("headerName", "Loan Fund Utilization");
    },
    loanSizeSelectFn() {
      this.isReturnOnSavings = false;
      this.isUtilization = false;
      this.isLoanSize = true;
      this.isMemberSavings = false;
      this.isMemberShareout = false;

      this.$store.dispatch("headerName", "Average Loan Size");
    },
    memberSavingsSelectFn() {
      this.isReturnOnSavings = false;
      this.isUtilization = false;
      this.isLoanSize = false;
      this.isMemberSavings = true;
      this.isMemberShareout = false;

      this.$store.dispatch("headerName", "Average Member Savings");
    },
    memberShareoutSelectFn() {
      this.isReturnOnSavings = false;
      this.isUtilization = false;
      this.isLoanSize = false;
      this.isMemberSavings = false;
      this.isMemberShareout = true;

      this.$store.dispatch("headerName", "Average Member Shareout");
    }
  }
};
</script>


<style scoped>
.card {
  box-shadow: none;
}
.card h4 {
  color: #738088;
}

.card p {
  color: #dbdce1;
}

.card:hover,
.is-active-card {
  border: 1px solid #68c7c2;
  border-top: 3px solid #68c7c2;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(235, 235, 235, 1);
  -moz-box-shadow: 0px 0px 20px 0px rgba(235, 235, 235, 1);
  box-shadow: 0px 0px 20px 0px rgba(235, 235, 235, 1);
}

.is-active-card h4 {
  color: #68c7c2;
}

.is-active-card p {
  color: #324b5a;
}
</style>
