<template>
  <div class="card has-background-dark is-shadowless">
    <div class="card-content">
      <div class="columns is-multiline mb0">
        <div class="column is-full has-text-light pr0 pl0" @click="navbarToggleFn">
          Group
          <span class="icon is-pulled-right">
            <i class="material-icons-round">keyboard_arrow_down</i>
          </span>
        </div>
      </div>

      <div class="columns is-multiline" v-if="nav.isGroup">
        <div class="column is-full pr0 pl0">
          <div class="control">
            <select v-model="numMembers">
              <option disabled>Select number</option>
              <option :value="n" v-for="n in 50" :key="n">{{n}}</option>
            </select>
            <label class="label">Number of members</label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <select v-model="currency">
              <option disabled>Select currency</option>
              <option value="TZS">TZS</option>
              <option value="RWF">RWF</option>
              <option value="UGX">UGX</option>
              <option value="PHP">PHP</option>
              <option value="CDF">CDF</option>
            </select>
            <label class="label">Currency</label>
            <i class="bar"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Group",
  data() {
    return {
      numMembers: 1,
      currency: "TZS"
    };
  },

  computed: {
    nav() {
      return this.$store.getters.nav;
    }
  },
  watch: {
    numMembers(value) {
      this.$store.getters.config.numMembers = value;
    },

    currency(value) {
      this.$store.getters.config.currency = value;
    }
  },

  mounted() {
    this.$store.getters.config.numMembers = this.numMembers;
    this.$store.getters.config.currency = this.currency;
  },

  methods: {
    navbarToggleFn() {
      this.$store.getters.nav.isGroup = !this.$store.getters.nav.isGroup;
      this.$store.dispatch("clickGroup", this.$store.getters.nav.isGroup);
    }
  }
};
</script>

<style scoped>
.card {
  width: 100%;
}
</style>
