<template>
  <div class="card has-background-dark is-shadowless">
    <div class="card-content">
      <div class="columns is-multiline mb0">
        <div class="column is-full has-text-light pr0 pl0" @click="navbarToggleFn">
          Cycle
          <span class="icon is-pulled-right">
            <i class="material-icons-round">keyboard_arrow_down</i>
          </span>
        </div>
      </div>
      <div class="columns is-multiline" v-if="nav.isCycle">
        <div class="column is-full pr0 pl0">
          <div class="control">
            <select>
              <option disabled>Select # of Months</option>
              <option :value="n" v-for="n in 24" :key="n">{{n}} Months</option>
            </select>
            <label class="label">
              Cycle length
              <i class="material-icons-round is-size-6 cursor is-right">help_outline</i>
            </label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <select>
              <option disabled>Select Frequency</option>
              <option value="weekly">Weekly</option>
              <option value="bi-weekly">Fortnightly</option>
              <option value="monthly">Monthly</option>
            </select>
            <label class="label">
              Meeting frequency
              <i
                class="material-icons-round is-size-6 cursor is-right"
              >help_outline</i>
            </label>
            <i class="bar"></i>
          </div>
        </div>
        <div class="column is-full pr0 pl0 pt0">
          <div class="control">
            <input
              class="input"
              type="text"
              value="Enter amount"
              oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
            />
            <label class="label">
              Initial investments
              <i
                class="material-icons-round is-size-6 cursor is-right"
              >help_outline</i>
            </label>
            <i class="bar"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Cycle",
  data() {
    return {
      meetingFreq: "weekly",
      // initSavingsDist:"Enter amount"
    };
  },
  computed: {
    nav() {
      return this.$store.getters.nav;
    }
  },

  watch: {
    meetingFreq(value) {
      this.$store.getters.config.meetingFreq = value;
    }
  },

  mounted() {
    this.$store.getters.config.meetingFreq = this.meetingFreq;
    // this.$store.getters.config.initSavingsDist = this.
  },

  methods: {
    navbarToggleFn() {
      this.$store.getters.nav.isCycle = !this.$store.getters.nav.isCycle;
      this.$store.dispatch("clickCycle", this.$store.getters.nav.isCycle);
    }
  }
};
</script>

<style scoped>
.card {
  width: 100%;
}
</style>
