<template>
  <section class="section pt0">
    <div class="container is-fluid">
      <div class="columns is-multiline">
        <div class="column is-3">
          <Sidemenu />
        </div>
        <div class="column is-7 mt20">
          <div class="columns mb0">
            <div class="column is-half">
              <div
                class="dropdown"
                :class="isClicked == true ? 'is-active' : ''"
                @click="navbarToggleFn"
              >
                <div class="dropdown-trigger">
                  <button class="button is-light" aria-haspopup="true">
                    <span>
                      <h4 class="is-size-4 has-text-primary">{{selectedSimulation}}</h4>
                    </span>
                    <span class="icon is-small">
                      <i class="material-icons-round ml30">keyboard_arrow_down</i>
                    </span>
                  </button>
                </div>
                <div class="dropdown-menu ml120" role="menu">
                  <div class="dropdown-content">
                    <a
                      class="dropdown-item has-text-weight-semibold"
                      @click="selectedSimulationFn(simulation)"
                      v-for="(simulation,index) in simulationArray"
                      :key="index"
                    >{{simulation}}</a>
                  </div>
                </div>
              </div>
              <i class="fa fa-edit ml50 mt10 has-text-grey-light cursor" style="font-size:24px"></i>
              <div class="columns is-mobile pt0 mr5 ml5 is-hidden" style="margin-top: -20px">
                <div class="column is-4 pr0 pt0">
                  <div class="control">
                    <input class="input" type="text" value="Simulation Name" required />
                    <i class="bar"></i>
                  </div>
                </div>
                <div class="column is-8 pl0 pt0" style="margin-left: -40px">
                  <i class="fa fa-edit ml50 mt30 has-text-info cursor" style="font-size:24px"></i>
                  <span class="has-text-info ml10 cursor">Save</span>
                </div>
              </div>
            </div>
            <div class="column is-quarter">
              <span class="tag is-medium cursor">
                <p>
                  <span class="icon is-left">
                    <i class="material-icons-round has-text-info">unarchive</i>
                  </span>
                  <span class="is-size-6 has-text-info has-text-weight-semibold pl10">Export Results</span>
                </p>
              </span>
            </div>
            <div class="column is-quater">
              <span class="tag is-medium cursor">
                <p>
                  <span class="icon is-left">
                    <i class="material-icons-round has-text-info">archive</i>
                  </span>
                  <span
                    class="is-size-6 has-text-info has-text-weight-semibold pl10"
                  >Save Simulation</span>
                </p>
              </span>
            </div>
          </div>
          <div class="columns mt0">
            <div class="column is-full pt0">
              <div class="card has-background-white is-shadowless mr10 ml10">
                <div class="card-content pt50 pl50 pr50 pb50">
                  <h4 class="is-size-4 has-text-grey has-text-weight-semibold">{{headerName}}</h4>
                </div>
                <div class="card-content">
                  <!-- {{results}} -->
                  <!-- <div id="line-chart"></div> -->
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="column is-2 mt20">
          <ResultsContent />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Sidemenu from "@/components/Home/Simulator.vue";
import ResultsContent from "@/components/Home/Results.vue";
import * as d3 from "d3";

export default {
  name: "home",
  components: {
    Sidemenu,
    ResultsContent
  },
  data() {
    return {
      isClicked: false,
      simulationArray: ["Simulation 1", "Simulation 2", "Simulation 3"],
      selectedSimulation: "Simulation 1",
      data: [],
      width: 960,
      height: 500
    };
  },

  computed: {
    headerName() {
      return this.$store.getters.headerName;
    },
    results(){
      return this.$store.getters.results
    }
  },
  mounted() {
    this.renderChart();
    setTimeout(() => {
      this.data = [
        { date: "1-May-12", close: 58.13 },
        { date: "30-Apr-12", close: 53.98 },
        { date: "27-Apr-12", close: 67.0 },
        { date: "26-Apr-12", close: 89.7 },
        { date: "25-Apr-12", close: 99.0 },
        { date: "24-Apr-12", close: 130.28 },
        { date: "23-Apr-12", close: 166.7 },
        { date: "20-Apr-12", close: 234.98 },
        { date: "19-Apr-12", close: 345.44 },
        { date: "18-Apr-12", close: 443.34 },
        { date: "17-Apr-12", close: 543.7 },
        { date: "16-Apr-12", close: 580.13 },
        { date: "13-Apr-12", close: 605.23 },
        { date: "12-Apr-12", close: 622.77 },
        { date: "11-Apr-12", close: 626.2 },
        { date: "10-Apr-12", close: 628.44 },
        { date: "9-Apr-12", close: 636.23 },
        { date: "5-Apr-12", close: 633.68 },
        { date: "4-Apr-12", close: 624.31 },
        { date: "3-Apr-12", close: 629.32 },
        { date: "2-Apr-12", close: 618.63 },
        { date: "30-Mar-12", close: 599.55 },
        { date: "29-Mar-12", close: 609.86 },
        { date: "28-Mar-12", close: 617.62 },
        { date: "27-Mar-12", close: 614.48 },
        { date: "26-Mar-12", close: 606.98 }
      ];
    }, 1000);
  },

  methods: {
    navbarToggleFn() {
      this.isClicked = !this.isClicked;
    },
    selectedSimulationFn(simulation) {
      this.selectedSimulation = simulation;
    },

    renderChart() {
      if (document.getElementsByTagName("svg")) {
        d3.selectAll("svg").remove();
      }
      var data = this.data;
      var margin = { top: 20, right: 20, bottom: 30, left: 50 };
      var width = this.width - margin.left - margin.right;
      var height = this.height - margin.top - margin.bottom;
      var parseTime = d3.timeParse("%d-%b-%y");

      // parse the date / time
      var parseTime = d3.timeParse("%d-%b-%y");

      // set the ranges
      var x = d3.scaleTime().range([0, width]);
      var y = d3.scaleLinear().range([height, 0]);

      // define the area
      var area = d3
        .area()
        .x(function(d) {
          return x(d.date);
        })
        .y0(height)
        .y1(function(d) {
          return y(d.close);
        });

      // define the line
      var valueline = d3
        .line()
        .x(function(d) {
          return x(d.date);
        })
        .y(function(d) {
          return y(d.close);
        });

      var svg = d3
        .select("#line-chart")
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      // format the data
      data.forEach(function(d) {
        d.date = parseTime(d.date);
        d.close = +d.close;
      });

      // scale the range of the data
      x.domain(
        d3.extent(data, function(d) {
          return d.date;
        })
      );
      y.domain([
        0,
        d3.max(data, function(d) {
          return d.close;
        })
      ]);

      // add the area
      svg
        .append("path")
        .data([data])
        .attr("class", "area")
        .attr("d", area);

      // add the valueline path.
      svg
        .append("path")
        .data([data])
        .attr("class", "line")
        .attr("d", valueline);

      // add the X Axis
      svg
        .append("g")
        .attr("transform", "translate(0," + height + ")")
        .call(d3.axisBottom(x));

      // add the Y Axis
      svg.append("g").call;
    }
  },
  watch: {
     data(val) {
      if (val){
        this.renderChart()
      }
     }
    }
};
</script>

<style >
.line {
      fill: none;
      stroke: steelblue;
      stroke-width: 2px;
    }

    .area {
      fill: white;
    }
</style>
