<template>
  <div class="card has-background-dark is-shadowless">
    <div class="card-content">
      <div class="columns is-multiline mb0">
        <div class="column is-full has-text-light pr0 pl0" @click="navbarToggleFn">
          Policy
          <span class="icon is-pulled-right">
            <i class="material-icons-round">keyboard_arrow_down</i>
          </span>
        </div>
      </div>

      <div class="columns is-multiline" v-if="nav.isPolicy">
        <div class="column is-full pr0 pl0">
          <div class="columns">
            <div class="column is-full">
              <div class="field has-addons has-addons-centered">
                <p class="control" style="width: 50%">
                  <a
                    class="button is-fullwidth is-size-7 is-light"
                    :class="shareBased != true ? 'is-outlined' : ''"
                    @click="setShareBased"
                  >
                    <span>Shared Based</span>
                  </a>
                </p>
                <p class="control" style="width: 50%">
                  <a
                    class="button is-fullwidth is-size-7 is-light"
                    :class="shareBased != false ? 'is-outlined' : ''"
                    @click="setCurrencyBased"
                  >
                    <span>Currency Based</span>
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
        <div class="column is-full pr0 pl0">
          <!-- Shared Based -->
          <div class="columns is-multiline">
            <div class="column is-full">
              <div class="control">
                <input
                  class="input"
                  type="text"
                  value="Enter share price"
                  v-model="sharePrice"
                  oninput="this.value = this.value.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');"
                />
                <label class="label">
                  Share price
                  <i class="material-icons-round is-size-6 cursor is-right">help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
            <div class="column is-full pt0">
              <div class="control">
                <select>
                  <option disabled>Select # of shares</option>
                  <option :value="n" v-for="n in 10" :key="n">{{n}}</option>
                </select>
                <label class="label">
                  Min. # of shares
                  <i
                    class="material-icons-round is-size-6 cursor is-right"
                  >help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
            <div class="column is-full pt0">
              <div class="control">
                <select>
                  <option disabled>Select # of shares</option>
                  <option :value="n" v-for="n in 10" :key="n">{{n}}</option>
                </select>
                <label class="label">
                  Max. # of shares
                  <i
                    class="material-icons-round is-size-6 cursor is-right"
                  >help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
          </div>

          <div class="columns is-multiline">
            <div class="column is-full pt0">
              <div class="control">
                <select v-model="oneTimeInterest">
                  <option disabled>Select method</option>
                  <option value="true">One Time Interest</option>
                  <option value="false">Monthly Interest</option>
                </select>
                <label class="label">
                  Interest method
                  <i
                    class="material-icons-round is-size-6 cursor is-right"
                  >help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
            <div class="column is-full pt0">
              <div class="control">
                <select v-model="interestRate">
                  <option disabled>Select interest rate</option>
                  <option :value="n+4" v-for="n in 46" :key="n">{{n+4}} %</option>
                </select>
                <label class="label">
                  Interest rate
                  <i
                    class="material-icons-round is-size-6 cursor is-right"
                  >help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
            <div class="column is-full pt0">
              <div class="control">
                <select v-model="maxLoanTermMonths">
                  <option disabled>Select # of months</option>
                  <option :value="n" v-for="n in 12" :key="n">{{n}} Months</option>
                </select>
                <label class="label">
                  Loan duration
                  <i
                    class="material-icons-round is-size-6 cursor is-right"
                  >help_outline</i>
                </label>
                <i class="bar"></i>
              </div>
            </div>
          </div>
          <div class="columns">
            <div class="column is-full pt0 pb0">
              <p class="is-right is-size-6 has-text-weight-semibold has-text-info cursor">View More</p>
            </div>
          </div>
          <!-- End Currency Based -->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Policy",
  data() {
    return {
      shareBased: true,
      sharePrice: "Enter share price",
      oneTimeInterest: true,
      interestRate: 5,
      maxLoanTermMonths : 1
    };
  },
  computed: {
    nav() {
      return this.$store.getters.nav;
    }
  },

  watch: {
    sharePrice(value) {
      this.$store.getters.config.sharePrice = Number(value);
    },

    oneTimeInterest(value) {
      if (value == "true") {
        this.$store.getters.config.oneTimeInterest = true;
      } else {
        this.$store.getters.config.oneTimeInterest = false;
      }
    },

    interestRate(value) {
      this.$store.getters.config.interestRate = value / 100;
    },

    maxLoanTermMonths(value){
      this.$store.getters.config.maxLoanTermMonths = value
    }
  },

  mounted() {
    this.$store.getters.config.shareBased = this.shareBased;
    this.$store.getters.config.sharePrice = 0;
    this.$store.getters.config.oneTimeInterest = this.oneTimeInterest;
    this.$store.getters.config.interestRate = this.interestRate / 100;
    this.$store.getters.config.maxLoanTermMonths = this.maxLoanTermMonths
  },

  methods: {
    navbarToggleFn() {
      this.$store.getters.nav.isPolicy = !this.$store.getters.nav.isPolicy;
      this.$store.dispatch("clickPolicy", this.$store.getters.nav.isPolicy);
    },

    setShareBased() {
      this.shareBased = true;
      this.$store.getters.config.shareBased = true;
    },

    setCurrencyBased() {
      this.shareBased = false;
      this.$store.getters.config.shareBased = false;
    }
  }
};
</script>

<style scoped>
.card {
  width: 100%;
}
</style>