import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {

    nav: {
      isGroup: false,
      isPolicy: false,
      isCycle: false,
      isPerformance: false,
    },

    config:{},

    headerName: 'Return on Savings',

    results: []
  },

  mutations: {

    setGroup(state, paylod) {
      state.nav.isGroup = paylod
      state.nav.isPolicy = false
      state.nav.isCycle = false
      state.nav.isPerformance = false
    },

    setPolicy(state, paylod) {
      state.nav.isGroup = false
      state.nav.isPolicy = paylod
      state.nav.isCycle = false
      state.nav.isPerformance = false
    },

    setCycle(state, paylod) {
      state.nav.isGroup = false
      state.nav.isPolicy = false
      state.nav.isCycle = paylod
      state.nav.isPerformance = false
    },

    setPerformance(state, paylod) {
      state.nav.isGroup = false
      state.nav.isPolicy = false
      state.nav.isCycle = false
      state.nav.isPerformance = paylod
    },

    setHeadername(state, paylod){
      state.headerName = paylod
    },

    setConfig(state, paylod){
      state.config = paylod
    },

    setResults(state, paylod){
      state.results = paylod
    }

  },

  actions: {
    clickGroup({ commit }, paylod) {
      commit('setGroup', paylod)
    },

    clickPolicy({ commit }, paylod) {
      commit('setPolicy', paylod)
    },

    clickCycle({ commit }, paylod) {
      commit('setCycle', paylod)
    },

    clickPeformance({ commit }, paylod) {
      commit('setPerformance', paylod)
    },

    headerName({commit}, paylod){
      commit('setHeadername', paylod)
    },

    config({commit}, paylod){
      commit('setConfig', paylod)
    },

    results({commit}, paylod){
      commit('setResults', paylod)
    }
  },

  getters: {
    nav(state) {
      return state.nav
    },
    headerName(state){
      return state.headerName
    },
    config(state){
      return state.config
    },
    results(state){
      return state.results
    }
  }
})
