<template>
  <nav class="panel" style="min-height: 100vh">
    <p class="panel-heading has-text-weight-bold" style="color:#ffbf30">Simulator</p>
    <p class="panel-block has-text-white has-text-weight-semibold pb40 pt40">Inputs</p>
    <div class="panel-block">
      <Group />
    </div>
    <div class="panel-block">
      <Policy />
    </div>
    <div class="panel-block">
      <Cycle />
    </div>
    <div class="panel-block" style="border-bottom: none">
      <Performance />
    </div>
    <div class="panel-block panel-shadow">
      <button
        class="button is-primary is-rounded glow-effect mb10"
        style="margin:0 auto;"
        @click="runSimulation"
      >Run Simulation</button>
    </div>
  </nav>
</template>

<style scoped>
.panel-shadow {
  -webkit-box-shadow: 0px -5px 5px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px -5px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px -5px 5px 0px rgba(0, 0, 0, 0.5);
  border: none;
}
</style>


<script>
import Group from "@/components/Home/SimulatorContent/GroupContent.vue";
import Policy from "@/components/Home/SimulatorContent/PolicyContent.vue";
import Cycle from "@/components/Home/SimulatorContent/CycleContent.vue";
import Performance from "@/components/Home/SimulatorContent/PerformanceContent.vue";
import simulatorService from "@/service/simulator.service.js";

export default {
  name: "home",
  components: {
    Group,
    Policy,
    Cycle,
    Performance
  },

  methods: {
    runSimulation() {
      console.log(this.$store.getters.config);
      var test = {
        numCycles: 1,
        numMembers: this.$store.getters.config.numMembers,
        meetingsPerCycle: 39,
        meetingFreq: this.$store.getters.config.meetingFreq,

        probAttendance: [0.85, 0.1, 0.05],

        fineAmount: 1000,
        probNumFines: [0.92, 0.05, 0.02, 0.01],
        finePaymentZeroSomeAllDist: [0.1, 0.1, 0.8],
        finePaymentSomeDist: 0.5,

        socFundRequired: true,
        socFundPayment: 1000,
        socFundVolZeroSomeAllDist: [0.2, 0.5, 0.3],
        socFundVolSomeDist: 0.5,

        probSocFundPayout: [0.93, 0.05, 0.02],
        socFundPayoutDist: 0.3,

        probMiscIncome: 0.05,
        miscIncomeDist: [20000, 1000, 2000, 40000],

        probMiscExpense: 0.1,
        miscExpenseDist: [5000, 500, 1000, 10000],

        maxLoanTermMonths: this.$store.getters.config.maxLoanTermMonths,
        minLoanFactor: 1,
        minLoanAmount: 100000,
        maxLoanFactor: 3,
        oneTimeInterest: this.$store.getters.config.oneTimeInterest,
        interestRate: this.$store.getters.config.interestRate,

        shareBased: this.$store.getters.config.shareBased,
        sharePrice: this.$store.getters.config.sharePrice,
        sharePurchaseDistrib: [0.1, 0.2, 0.1, 0.1, 0.2, 0.3],

        probSavings: 0.8,
        maxSavingsPerMemberPerMtg: 50000,
        savingsDistrib: [20000, 1000, 10000, 50000],

        loanWillingness: 0.8,
        loanAmountDist01: 0.8,

        probMakingPaymentPerMonth: [0.05, 0.2, 0.95],
        loanPaymentSomeAllDist: [0.2, 0.8],
        loanPaymentSomeDist: 0.5,
        probFullPaymentAtEnd: 0.95,

        probDefault: 0.05,
        initSavingsDist: [0, -1, 0, 0],

        currency: this.$store.getters.config.currency
      };
      simulatorService
        .simulationAPIcall(test)
        .then(response => {
          this.$store.dispatch("results", response.data);
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>

<style scoped>
.panel-block {
  /* -webkit-box-align: center;
    -ms-flex-align: center; */
  align-items: center;
  color: #f8e9c5;
  /* display: -webkit-box;
    display: -ms-flexbox; */
  display: flex;
  /* -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start; */
  padding: 1.5em 2em 0.75em 2em;
  background-color: #324b5a;
  justify-content: space-between;
}
</style>
